/* Homepage styles */
.homepage {
  position: relative;
  height: 100vh;
  background-image: url('img/bg3.jpg');
  background-size: cover;
  background-position: center;
  overflow: hidden; 
}

.homepage::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: black;
  opacity: 1;
  animation: fadeIn 3s forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}